body{
  background: #9266ff;
}
.app{
  background: #3f3160;
  width: 400px;
  margin: 40px auto; padding: 10px;
}
#to-do-list input {
  background-color: rgb(27, 112, 137);
  border: 0px;
  width: 200px;
  height: 30px; padding: 0 20px; margin: 20px; color: #fff;
}
#to-do-list ::placeholder{
  color: rgb(255, 255, 255, 0.5);
}
#to-do-list input:focus, #to-do-list button{
  outline: none;
}
#to-do-list button{
  width: 60px;
  height: 30px;
  border: 0px;
  border-radius: 5px;
  background-color: rosybrown; cursor: pointer; font-weight: bold; color: #fff;
}